import React from "react";
import "./App.css"
import "./index.css"
import UserProfile from "./UserProfile"


const API = 'https://api.github.com/users';







class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      username: 'panda5145',
      name:'',
      avatar:'',
      location:'',
      repos:'',
      followers: '',
      following:'',
      homeUrl:'',
      notFound:''
    }
  }




  fetchProfile(username) { 
    let url = `${API}/${username}`;
    fetch(url)
      .then((res) => res.json() )
      .then((data) => {
        this.setState({
          username: data.login,
          name: data.name,
          avatar: data.avatar_url,
          location: data.location,
          repos: data.public_repos,
          followers: data.followers,
          following: data.following,
          homeUrl: data.html_url,
          notFound: data.message
        })
      })
  }
  componentDidMount() {
    this.fetchProfile(this.state.username);
  }
  render() {
    return (
      <div>
         <section id="card">
           <SearchProfile fetchProfile={this.fetchProfile.bind(this)}/>
           <UserProfile data={this.state} />
         </section>
          <span className="panda">GitUserSearch - Created By <a href="https://facebook.com/chaman.pandey" target="_blank" rel="noopener noreferrer" title="Chaman">Chaman Pandey</a></span>
      </div>
    )
  }
}
class SearchProfile extends React.Component {
  render() {
    return (
      <div className="search--box">
         <form onSubmit={this.handleForm.bind(this)}>
           <label><input type="search" ref="username" placeholder=" Git Username"/></label>
         </form>
      </div>
    )
  }
  
  handleForm(e) {
   e.preventDefault();
    let username = this.refs.username.value
    this.props.fetchProfile(username);
    this.refs.username.value = '';
  }
}





export default App;


